// VARIABLES
// colors currently used
$light-blue: #95cbee;
$light-blue-2: #abdaf4;
$blue: #4682b4;
$dark-blue: #11315d;
$light-gray-2: #f2f2f2;

// general colors
$gray: #999999;
$gray-2: #b3b3b3;
$light-gray: #efefef;
$dark-gray: #333;
$light-red: #dcc4cb;
$red: #67001f;
$green: darkgreen;
$white: #fff;
$cyan: #52a8e6;
$light-cyan: #d9edf8;

// functional colors
$selected-blue: #449fb7;
$dark-selected-blue: $dark-blue;
$no-data-gray: #b3b3b3;

// general styles
$border: 1px solid #ccc;
$border-radius: 5px;

// make colors available to JS
:export {
  red: $red;
  lightred: $light-red;
  darkblue: $dark-blue;
  white: $white;
  cyan: $cyan;
  lightcyan: $light-cyan;
  noDataGray: $no-data-gray;
  gray: $gray;
  darkSelectedBlue: $dark-selected-blue;
}

// MIXINS
@mixin spaceItemsVert($size) {
  > :not(:first-child) {
    margin-top: $size;
  }
}
@mixin spaceItemsHoriz($size) {
  > :not(:last-child) {
    margin-right: $size;
  }
}

@mixin borderItemsVert($size) {
  > :not(:last-child) {
    margin-bottom: $size;
    padding-bottom: $size;
    border-bottom: $border;
  }
}

@mixin horiz() {
  display: flex;
  flex-flow: row;
}

@mixin boxShadow() {
  box-shadow: 1px 1px 5px 0 rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 1px 1px 5px 0 rgba(0, 0, 0, 0.25);
}

@mixin btn-primary() {
  border: 2px solid transparent;
  border-radius: 4px;
  background: -moz-linear-gradient(top, #425379 0%, #213561 100%);
  background: -ms-linear-gradient(top, #425379 0%, #213561 100%);
  background: -o-linear-gradient(top, #425379 0%, #213561 100%);
  background: -webkit-linear-gradient(top, #425379 0%, #213561 100%);
}
